.ProfileWrapper {
  display: flex;
  flex-wrap: wrap;
  /* flex-direction: column; */
  /* align-items: center; */
  /* justify-content: center; */
}
.LogoutButton {
  background-color: rgb(115, 115, 51);
  border: none;
  padding: 10px 20px;
  border-radius: 10px;
  margin-top: 10px;
  cursor: pointer;
}
.EditDeleteProfileWrapper {
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.EditDeleteProfileWrapper button {
  margin: 5px;
  color: white;
  padding: 10px;
  border-radius: 10px;
  border: none;
}
.EditDeleteProfileWrapper button:first-child {
  background: blue;
}
.EditDeleteProfileWrapper button:nth-child(2) {
  background: red;
}
.ActiveButton {
  background-color: green !important;
}
.leftOfProfile {
  width: 20vw;
  background-color: white;
  height: 100vh;
}
.leftOfProfile li:nth-child(2) {
  background-color: rgb(25, 118, 210);
  color: white;
}
